// src/components/Hero.js
import React from "react";
import portrait from "../assets/img/portrait.jpg";
import selfie from "../assets/img/selfie.png";
import { ReactTyped } from "react-typed";
import { useEffect, useState } from "react";
import { Link } from "react-scroll";
import Navbar from "./Navbar";
import ParticlesComponent from "./ParticlesComponent";

const stringsToRender = [
	"developer",
	"QA Engineer",
	"tester",
	"programmer",
	"student"
];

const colors = [
	"#0066ff", // Bright blue
	"#3399ff", // Light blue
	"#66ccff", // Lighter blue
	"#99ccff" // Very light blue
];

const Hero = () => {
	const [someState, setSomeState] = useState(false); // Example state
	const [colorIndex, setColorIndex] = useState(0);
	const [viewportHeight, setViewportHeight] = useState(window.innerHeight);

	useEffect(() => {
		const interval = setInterval(() => {
			setColorIndex((prevIndex) => (prevIndex + 1) % colors.length);
		}, 2500);

		return () => clearInterval(interval); // Clear the interval on unmount
	}, [colors.length]);
	useEffect(() => {
		const handleResize = () => {
			setViewportHeight(window.innerHeight); // Set the viewport height dynamically
		};
		console.log("viewportHeight", viewportHeight);
		window.addEventListener("resize", handleResize); // Listen for window resize

		return () => {
			window.removeEventListener("resize", handleResize); // Cleanup event listener
		};
	}, []);
	return (
		<section id="hero">
			<div className="fixed top-0 left-0 w-full h-20 bg-gray-900 z-0">
				{/* Transparent banner background */}
			</div>
			<Navbar />
			<ParticlesComponent />

			<section className="mt-[76px] flex-col justify-center items-center sm:h-[calc(100vh-76px)] py-20  px-10 bg-gray-900 text-white flex">
				<div className="flex md:flex-row flex-col justify-center md:m-[6rem] lg:ml-[20rem] md:gap-[30rem] items-center space-y-10  ">
					<div className="md:w-1/2 w-full flex justify-center lg:justify-end">
						<img
							src={selfie} // Use your image path here
							alt="Picture of me"
							className="w-2/4 md:w-full rounded-full object-cover border-4 border-gray-400 shadow-lg z-40"
						/>
					</div>

					<div className="md:border-l-4 border-gray-300 h-[200px] absolute ml-[120px]"></div>

					<div className="w-full flex gap-2 flex-col justify-center md:space-y-3 px-2">
						{/* Static Text */}
						<hr className="md:hidden"></hr>
						<h1 className="md:text-7xl text-4xl flex ">
							Hello{" "}
							<span role="img" aria-label="wave" className="inline-block">
								👋
							</span>
						</h1>
						<h2 className="md:text-3xl text-2xl w-3/4 flex font-semibold">
							My name is Rawhi Alfar
						</h2>
						<h3 className="md:text-3xl flex">
							I'm a{" "}
							<span
								className="ml-[8px] text-gradient font-bold"
								style={{
									color: colors[colorIndex],
									transition: "color 2s ease"
								}}
							>
								<ReactTyped
									strings={stringsToRender}
									typeSpeed={100}
									backSpeed={100}
									loop
								/>
							</span>
						</h3>
					</div>
				</div>

				<Link
					to="about"
					smooth={true}
					duration={500}
					offset={-100}
					className="scroll-mt-16 relative z-10 arrow-icon text-4xl 2xl:pt-[5rem] cursor-pointer transition-transform duration-300 ease-in-out hover:translate-y-2 hover:opacity-75"
				>
					<i className="bx bxs-chevron-down"></i>
				</Link>
			</section>
		</section>
	);
};

export default Hero;
